%h1 {
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 85%;
  letter-spacing: -0.05em;
  text-transform: uppercase;
}

%h2 {
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: normal;
  line-height: 85%;
  letter-spacing: -0.05em;
  text-transform: uppercase;
}

%mainText {
  font-size: 16px;
  line-height: 150%;
  letter-spacing: -0.01em;
}

%bigText {
  font-weight: normal;
  font-size: 96px;
  line-height: 85%;

  letter-spacing: -0.05em;
  text-transform: uppercase;
}

%upperCaseText {
  font-weight: 500;
  font-size: 16px;
  line-height: 100%;
  letter-spacing: -0.02em;
  text-transform: uppercase;
}

%background-image {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
